html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  font-family: var(--font-josefin-sans);
  background-color: var(--check-bg-linear-gradient);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --bright-blue: hsl(220, 98%, 61%);
  --check-bg-linear-gradient: linear-gradient(
    to right,
    hsl(192, 100%, 67%),
    hsl(280, 87%, 65%)
  );
  --lt-very-light-gray: hsl(0, 0%, 98%);
  --lt-very-light-grayish-blue: hsl(236, 33%, 92%);
  --lt-light-grayish-blue: hsl(233, 11%, 84%);
  --lt-dark-grayish-blue: hsl(236, 9%, 61%);
  --lt-very-dark-grayish-blue: hsl(235, 19%, 35%);
  --dt-dark-blue: hsl(235, 21%, 11%);
  --dt-dark-desaturated-blue: hsl(235, 24%, 19%);
  --dt-light-grayish-blue: hsl(234, 39%, 85%);
  --dt-light-grayish-blue-hover: hsl(236, 33%, 92%);
  --dt-grayish-blue-400: hsl(234, 11%, 52%);
  --dt-grayish-blue-500: hsl(233, 14%, 35%);
  --dt-grayish-blue-600: hsl(237, 14%, 26%);
}

:root {
  --font-josefin-sans: "Josefin Sans", sans-serif;
}

.card {
  padding: 1.25rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}